<template>
  <div class="contact-wrapper" ref="contactContainer">
    <app-bg
        :height="187" src="contact/4_02.jpg"/>

      <div class="form-box">
          <div class="form-body">
              <div class="form-row f-row-between">
                  <div class="form-row f-row-start" style="width:62%;">
                      <label class="f-mr16">:</label>
                      <input type="text" v-model="name" class="form-input" />
                  </div>
                  <div class="form-row half f-row-between">
                      <div class="f-row-between radio-item" style="width: 0.46rem; margin-right: 20px">
                        <label for="M"></label>
                        <span class="simulation" :style="{backgroundColor: sex === 'M' ? '#fb3a6f' : '#f5f1ee', borderWidth: sex === 'M' ? 0 : '2px'}"></span>
                        <input type="radio" value="M" v-model="sex"/>
                      </div>
                      <div class="f-row-between  radio-item" style="width: 0.46rem;">
                        <label for="F"></label>
                        <span class="simulation" :style="{backgroundColor: sex === 'F' ? '#fb3a6f' : '#f5f1ee', borderWidth: sex === 'F' ? 0 : '2px'}"></span>
                        <input type="radio" value="F" v-model="sex"/>
                      </div>
                  </div>
              </div>
              <div class="form-row f-row-start f-mt20">
                  <label class="f-mr16">:</label>
                  <input type="text"  v-model="phone" class="form-input" />
              </div>
              <div class="form-row f-row-start f-mt20">
                  <label class="f-mr16">:</label>
                  <select class="form-select" v-model="province">
                  	<option value="">----</option>
                    <option v-for="(p, i) in plist" :value="p.value" :key="i">{{p.label}}</option>
                  </select>
                  <select class="form-select" v-model="city" style="margin-left: -1px">
                  	<option value="">----</option>
                    <option v-for="(c, i) in clist" :value="c.value" :key="i">{{c.label}}</option>
                  </select>
                  <select class="form-select" v-model="area" style="margin-left: -1px">
                  	<option value="">----</option>
                    <option v-for="(a, i) in alist" :value="a.value" :key="i">{{a.label}}</option>
                  </select>
              </div>
              <div class="form-row f-row-start f-mt20">
                  <label class="f-mr16">:</label>
                  <input type="text" class="form-input" v-model="budget"/>
                  <label style="padding-left: 10px" ></label>
              </div>
              <div class="form-row f-row-start f-mt20" style="alignItems: flex-start">
                  <label class="f-mr16">:</label>
                  <textarea class="form-textarea" v-model="message"></textarea>
              </div>
              <div class="submit-btn-wrap f-row-center">
                  <button type="button" name="button" @click="submit"></button>
              </div>
          </div>
      </div>
      <div class="blank"></div>

  </div>
</template>

<script>
import AppBg from '@/components/AppBg'
import axios from 'axios'
import pca from '@/libs/area.json'

export default {
  data() {
    return {
      name: '',
      message: '',
      phone: '',
      budget: '',
      sex: 'M',
      province: '',
      city: '',
      area: '',
      pIdx: -1,
      cIdx: -1,
      aIdx: -1,
      submiting: false
    }
  },
  name: 'contact',
  created() {
  },
  mounted() {
  },
  computed: {
    plist() {
      return pca
    },
    clist() {
      const pidx = pca.findIndex(v => v.value === this.province)
      if (this.pIdx !== pidx) {
        this.city = ''
        this.area = ''
      }
      this.pIdx = pidx
      return typeof pca[pidx] === 'undefined' ? [] : pca[pidx].children
    },
    alist() {
      let cityItem = this.pIdx === -1 ? false : pca[this.pIdx]
      cityItem = cityItem ? cityItem.children : []
      const cidx = cityItem.findIndex(v => v.value === this.city)
      if (this.cIdx !== cidx) {
        this.area = ''
      }
      this.cIdx = cidx
      return typeof cityItem[cidx] === 'undefined' ? [] : cityItem[cidx].children
    }
  },
  methods:{
    resetValue() {
      this.name = '',
      this.message = '',
      this.phone = '',
      this.budget = '',
      this.sex = 'M',
      this.province = '',
      this.city = '',
      this.area = '',
      this.pIdx = -1,
      this.cIdx = -1,
      this.aIdx = -1
    },
    trim(str = '') {
      if (typeof str === 'string') {
        return str.replace(/(^\s*)|(\s*)$/g, '')
      }
    },
    getLabelByValue() {
      let provinceName = '', cityName = '', areaName = '';
      let pItem
      let cItem
      if (!!this.province) {
        provinceName = pca[this.pIdx].label
        pItem = pca[this.pIdx]
      }
      if (!!this.city) {
        cityName = pItem.children[this.cIdx].label
        cItem = pItem.children[this.cIdx]
      }
      if (!!this.area) {
        areaName = (cItem.children.find(v => v.value === this.area)).label
      }
      return {
        provinceName,
        cityName,
        areaName
      }
    },
    submit() {
      if(!this.verifyParams()) return
      const selectedPca = this.getLabelByValue()
      const params = {
        name: this.name,
        phone: this.phone,
        sex: this.sex,
        money: parseInt(this.budget),
        province:  selectedPca.provinceName,
        city: selectedPca.cityName,
        region: selectedPca.areaName,
        message: this.message
      }
      const self = this
      if (this.submiting) return
      this.submiting = true
      this.$http.post('/unauth/add', params)
        .then(function (response) {
          this.submiting = false
          if (response.data.success) {
            // self.resetValue()
            alert('')
          }
        })
        .catch(err => {
          this.submiting = false
          console.log(err)
        })
    },
    verifyParams() {
      if (this.trim(this.name) === '') {
        alert('')
        return false
      } else if (this.trim(this.phone) === '') {
        alert('')
        return false
      }
      if (this.name.length > 8) {
        alert('1-8')
        return false
      } else if (!/^1[34578]\d{9}$/.test(this.phone)) {
        alert('')
        return false
      }
      if (this.trim(this.budget) !== '') {
        if (isNaN(this.budget) || !/^[1-9]\d*$/.test(this.budget)) {
          alert('')
          return false
        }
      }
      return true
    }
  },
  components: {
    AppBg
  }
}
</script>


<style scoped lang="less">
.contact-wrapper{
  margin-top: -12px;
  .form-box{
      width: 100%;
      padding: 20px 10px;
      height: 3.5rem;
      margin-top: 50px;
      background-color: #f7f6f1;
  }
  .form-body{
    padding: 10px;
    width: 100%;
    height: 100%;
    border: 1px solid #7a7977;
  }
  .f-mt20{
    margin-top: 0.2rem;
  }
  .f-mr16{
    margin-right: 0.16rem;
  }
  input[type=radio]{
    width: 0.1rem;
    height: 0.1rem;
    position: relative;
    opacity: 0;
    z-index: 5;
  }
  .radio-item{
    position: relative;

  }
  .simulation{
    position: absolute;
    width: 0.1rem;
    height: 0.1rem;
    border-radius: 50%;
    background-color: #fb3a6f;
    right: 0;
    top: 0.02rem;
    border: 2px solid #8e8d89;
  }
  .form-row{
    label{
      font-size: 0.14rem;
      color: #898884;
    }
    input[type=text]{
      width: 1.3rem;
      height: 0.24rem;
      font-size: 0.14rem;
      padding-left: 0.05rem;
      background-color: #f7f6f1;
      border: 1px solid #93928e;
    }
  }
  .form-textarea{
      flex: 1;
      font-size: 0.14rem;
      padding: 0.05rem;
      border: 1px solid #93928e;
      background-color: #f7f6f1;
      height: 0.6rem;
  }
  .blank{
    height: 80px;
    background-color: #f7f6f1;
  }
  .form-select{
    width: 0.8rem;
    height: 0.24rem;
    border: 1px solid #93928e;
    font-size: 0.14rem;
    border-radius: 0;
    background-color: #f7f6f1;
  }
  .submit-btn-wrap{
    margin-top: 0.16rem;
    button{
      font-size: 0.15rem;
      color: #fff;
      background-color: #ff3a6f;
      border:0;
      padding: 0.06rem;
      font-weight: lighter;
    }
  }
}
</style>



